.non-resume h1 {
  display: none;
}
#landing {
  text-align: center;
}
#landing .avatar img {
  border: 5px solid #ffffff;
  border-radius: 50%;
  margin-top: 50px;
  width: 30%;
  -webkit-box-shadow: 0px 6px 13px 0px #dbdbdb;
  box-shadow: 0px 6px 13px 0px #dbdbdb;
}
@media only screen and (max-width: 600px) {
  #landing .avatar img {
    width: 60%;
  }
}
@media screen and (min-width: 601px) and (max-width: 800px) {
  #landing .avatar img {
    width: 40%;
  }
}
#location p:first-of-type {
  margin-top: 50px;
}
#location .map {
  -webkit-box-shadow: 0px 6px 13px 0px #dbdbdb;
  box-shadow: 0px 6px 13px 0px #dbdbdb;
  height: 400px;
  margin-left: 12.5%;
  width: 75%;
}
@media only screen and (max-width: 600px), screen and (min-width: 601px) and (max-width: 800px) {
  #location .map {
    width: 90%;
    margin-left: 5%;
  }
}
#skills .tabs .tabs-navigation {
  float: bottom;
  width: 100%;
}
#skills .tabs .tabs-navigation button {
  font-size: 1.2em;
}
#skills .tabs .tabs-content table {
  font-size: 1.75em;
  width: 100%;
  white-space: nowrap;
}
#skills .tabs .tabs-content table tr td {
  padding: 10px;
  text-align: left;
}
#skills .tabs .tabs-content table tr td:first-of-type {
  line-height: 2.5em;
  text-align: right;
  width: 40%;
}
#skills .stars {
  color: #fdd43b;
  font-size: 1.8em;
  letter-spacing: 15px;
}
@media screen and (min-width: 601px) and (max-width: 800px) {
  #skills .stars {
    font-size: 1.2em;
  }
}
h1,
h2,
h3 {
  color: #686868;
  margin: 20px;
}
h2 {
  font-size: 3.5em;
  font-weight: 400;
}
h3 {
  font-size: 2.5em;
  font-weight: 400;
}
p {
  font-size: 1.2em;
  text-align: center;
}
.Section {
  color: #686868;
  font-size: 1.2em;
  margin-bottom: 50px;
}
.dark-text {
  color: #686868;
}
.slight-shadow {
  -webkit-box-shadow: 0px 6px 13px 0px #dbdbdb;
  box-shadow: 0px 6px 13px 0px #dbdbdb;
}
/* Style the buttons that are used to open the tab content */
.tabs {
  text-align: center;
}
.tabs .tabs-navigation button {
  background: inherit;
  border: none;
  border-bottom: 2px solid #ffffff;
  color: #d8d8d8;
  cursor: pointer;
  margin: 0 15px 0 15px;
  outline: none;
  padding: 15px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
.tabs .tabs-navigation button:hover {
  color: #a8a8a8;
  border-bottom: 2px solid #a8a8a8;
}
.tabs .tabs-navigation button.active {
  color: #686868;
  border-bottom: 2px solid #686868;
}
.tabs .tabs-content {
  margin: 30px;
}
body {
  margin: 0px 20px;
  padding: 0;
  font-family: Lato, Helvetica, sans-serif;
}


